import Head from 'next/head';
import React, { Component, Fragment } from "react";
import { PageTransition } from 'next-page-transitions'
import { TweenMax, TimelineMax, Ease, Power4 } from 'gsap';
import PropTypes from 'prop-types';
import ReactGA from 'react-ga';
// import Navigation from '../components/Navigation/Navigation'
import Menu from '../components/Menu/Menu';
import BackgroundPattern from '../components/BackgroundPattern/BackgroundPattern';
import Stats from '../util/stats';
import detect from '../util/detect/index.js';
import settings from '../util/settings';

export default class Page extends Component {

  componentDidMount() {
    if (settings.isDev) Stats();
    detect.init();
    document.body.className = [...new Set([...document.body.className.split(' '), ...detect.classes()])].join(' ');
    ReactGA.initialize(settings.GACcode)
    ReactGA.pageview(window.location.pathname)

    // this.transitionIn();
  }
  
  transitionIn = (childTransitionContainer, skip = false) => {
    this.childTransitionContainer = childTransitionContainer
    // if (skip) {
      // TweenMax.set(this.transitionContainer,{ x: 0, opacity: 1 });
      // TweenMax.set(this.childTransitionContainer.childNodes, { x: 0 })
      // TweenMax.set(this.transitionContainer, { transform: 'none' })
    // } else {
    //   // TweenMax.set(this.transitionContainer, { x: window.innerWidth, opacity: 1 })
    //   TweenMax.set(this.transitionContainer, { opacity: 1 })
    //   TweenMax.set(this.childTransitionContainer.childNodes, { x: 0.035 * window.innerWidth })
    //   // TweenMax.to(this.transitionContainer, 1, { x: 0, ease: Power4.easeOut });
    //   TweenMax.staggerTo(this.childTransitionContainer.childNodes, 1, { x: 0 }, .2)
    //   setTimeout(() => {
    //     TweenMax.set(this.transitionContainer, { transform: 'none' })
    //   }, 1000)
    // }
  }

  transitionOut = (callback = () => {}) => {
    const tl = new TimelineMax({ paused: true, onComplete: callback })
    tl.staggerTo(this.childTransitionContainer.childNodes, .5, { x: -50, autoAlpha: 0 }, .2)
    tl.to(this.transitionContainer, 1, { x: -window.innerWidth, ease: Power4.easeIn, delay: -.75 });
    tl.play();
  }

  render() {
    const { id, children, menu, showMenu = true } = this.props;
    return (
      <div id={id || ""} className='Page'
        ref={e => this.transitionContainer = e}
        style={{
          width: '100%',
          // opacity: 0
        }}
      >
        {/* <BackgroundPattern />
        { showMenu &&
          <Menu location={settings.isServer() ? {} : window.location} />
        } */}
        <div className='container' style={{ width: '100%' }}>
          { children }
        </div>
      </div>
    )
  }
}

Page.propTypes = {
  id: PropTypes.string,
  children: PropTypes.array,
  className: PropTypes.string,
  menu: PropTypes.object
}

Page.defaultProps = {
  id: '',
  children: [],
  className: '',
  menu: {}
}