import React, { Component } from 'react';
import router from 'next/router';

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true
    }
  }
  componentDidMount() {
    this.checkVisibility();
  }
  componentDidUpdate() {
    this.checkVisibility();
  }
  checkVisibility() {
    let visible = true;
    if (router.router.pathname === '/') {
      visible = false;
    }
    if (visible !== this.state.visible) this.setState({ visible })
  }
  render () {
    const { visible } = this.state;
    return visible && (
      <div className='BackgroundPattern-container d-none d-md-flex'>
        <div className='BackgroundPattern container'>
          <div className='offset-1 col-1'/>
          <div className='offset-2 col-1'/>
          <div className='offset-5 col-1'/>
        </div>
      </div>
    )
  }
}