import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TweenMax, TimelineMax, Ease, Power0 } from 'gsap';
import LogoOutline from '../../public/assets/svg/logo-outline.svg?include'
import detect from '../../util/detect'

export default class BrandOutline extends Component {
  componentDidMount() {
    const { animate } = this.props
    const paths = this.BrandOutline.childNodes[0].children
    if (animate) {
      TweenMax.to(paths, 3, { strokeDashoffset: 0, opacity: 1, delay: .2, onComplete: () => {
        // TweenMax.to(paths, 10, { strokeDasharray: 100 })
        // const tl = new TimelineMax({ paused: true, repeat: -1 })
        // tl.to(paths, 10, { strokeDashoffset: 650 })
        // tl.to(paths, 10, { strokeDashoffset: 0 })
        // tl.to(paths, 10, { strokeDashoffset: 150, ease: Power0.easeInOut })
        // tl.to(paths, 10, { strokeDashoffset: 0, ease: Power0.easeInOut })
        // tl.play();
      }})
    } else {
      TweenMax.set(paths, { strokeDashoffset: 0, opacity: 1 })
    }

    detect.init();
    if(detect.device() === 'desktop') window.addEventListener('mousemove', this.handleMouseMove)
  }

  componentWillUnmount() {
    if(detect.device() === 'desktop') window.removeEventListener('mousemove', this.handleMouseMove)
  }

  handleMouseMove = e => {
    const halfScreen = window.innerWidth / 2
    const x = e.clientX - halfScreen
    const offset = (x / halfScreen) * 30 * -1
    TweenMax.to(this.BrandOutline, .6, { transform: `translateX(${offset}px)` })
  }

  render() {
    return <span ref={e => this.BrandOutline = e} className='BrandOutline'>
    {/* dangerouslySetInnerHTML={{ __html: LogoOutline }} */}
      <LogoOutline />
    </span>
  }
}

BrandOutline.propTypes = {
  aniamte: PropTypes.bool,
}

BrandOutline.defaultProps = {
  animate: false,
}