import React, { Component, Fragment } from 'react';
import router from 'next/router';
import PropTypes from 'prop-types';
import { TimelineMax, TweenMax, Expo, Power3 } from 'gsap';
import Link from 'next/link'
import settings from '../../util/settings';
import Brand from '../Brand/Brand'
import BackgroundPattern from '../BackgroundPattern/BackgroundPattern';

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: true
    }
    this.MIN_SCREEN_WIDTH = 575;
    this.links = [];
    this.MENU_HEIGHT = 85;
    this.menuHeight = 0;
    this.animOn = false;
    this.pages = [
      {
        path: "projects",
        desc: 'what we can make'
      }, 
      {
        path: "services",
        desc: 'what we can do'
      },
      {
        path: "contact",
        desc: 'what we can help with'
      }
    ]
    this.tl = new TimelineMax();
    this.ASSET_PATH = settings.ASSET_PATH + "images/"
  }

  componentDidUpdate() {
    if (this.state.collapsed) this.menuHeight = this.Menu.getBoundingClientRect().height;
  }

  // componentWillUnmount() {
  //   this.tl.kill();
  // }

  toggleMenu = (action, callback) => {
    if (this.animOn) return;
    this.tl = new TimelineMax({paused: true, onComplete: ()=>{
              if (!this.state.collapsed) {
                document.body.style.overflow = "auto"
              }
              this.animOn = false;
              this.setState({collapsed: !this.state.collapsed})
            }});
    let chldrn = this.MenuBtn ? this.MenuBtn.children : [],
        time = 0.35;

    this.animOn = true;
    if (this.state.collapsed || (action && action == "open")) {
      document.body.style.overflow = "hidden"
      this.subcontainer.style.display = 'block';
      this.tl.set(this.linkContainer, { autoAlpha: 1, display: "block" });
      this.tl.to(chldrn[1], time * .7, { strokeDashoffset: 68 })
      this.tl.to([chldrn[0], chldrn[2]], time * .7, { strokeDashoffset: 0, delay: -time })
      this.tl.to(chldrn[0], time * .7, { y: 20 })
      this.tl.to(chldrn[2], time * .7, { y: -20, delay: -time * .7 })
      this.tl.to(this.Menu, time, { height: "100vh", backgroundColor: settings.colors.secondary, ease: Expo.easeOut, delay: -time })
      this.tl.to(chldrn[0], time, { rotationZ: "-45deg", transformOrigin: "center center", ease: Expo.easeOut })
      this.tl.to(chldrn[2], time, { rotationZ: "45deg", transformOrigin: "center center", ease: Expo.easeOut, delay: -time })
      this.tl.staggerFromTo(this.linkContainer.children, time, {autoAlpha: 0, y: -20}, { autoAlpha: 1, y: 0, ease: Expo.easeOut }, 0.1)
    } else if (!this.state.collapsed || (action && action == "close")) {
      this.tl.to([chldrn[0], chldrn[2]], time, { rotationZ: "0deg", transformOrigin: "center center", ease: Expo.easeIn})
      this.tl.staggerTo(this.linkContainer.children, time, {autoAlpha: 0, y: -10, ease: Expo.easeOut, delay: -time }, 0.1)
      this.tl.to(this.Menu, time, {height: this.menuHeight || this.MENU_HEIGHT, ease: Expo.easeOut, delay: -time })
      this.tl.to(chldrn[0], time, { y: -1, delay: -time })
      this.tl.to(chldrn[2], time, { y: 1 })
      this.tl.to(this.Menu, time, { backgroundColor: 'transparent', ease: Expo.easeOut })
      this.tl.to(chldrn[2], time, { strokeDashoffset: 20, ease: Power3.easeInOut, delay: -time * 1.5 })
      this.tl.to(chldrn[1], time, { strokeDashoffset: 35, ease: Power3.easeInOut, delay: -time/2 })
      this.tl.to(chldrn[0], time, { x: 70, ease: Power3.easeIn, delay: -time * 2 })
      this.tl.set(chldrn[0], { x: -70 })
      this.tl.to(chldrn[0], time, { x: 0, ease: Power3.easeOut, onComplete: ()=> {
        this.tl.set(this.linkContainer, { autoAlpha: 0, display: "none" });
        this.subcontainer.style.display = 'none';
      }})
      document.body.style.overflow = "auto"
    }
    this.tl.play();
    
    if (typeof callback == "function") callback();
    // if (typeof callback == "function") setTimeout(callback, (this.tl._totalDuration/4 || time) * 1000);
  }

  goTo = (e, url) => {
    e.preventDefault();
    this.toggleMenu('close', () => router.push(url))
  }

  getLink = (page, index) => {
    if (page) {
      const { path, desc } = page
      const title = path.replace(/\-/g, ' ');
      const link = `/${path === 'home' ? '' : path}`;
      const active = !settings.isServer() && router.pathname.substr(1) === path
      return (
        // <Link href={link} ref={el => this.links[path] = el}>
          <a className={`mainlink col-12 ${active ? 'active' : ''}`}
            href={link}
            onClick={e => this.goTo(e, link)}
            >
            <h1>
            { title }
            </h1>
            <p>{ desc }</p>
          </a>
        // </Link>
      )
    }
  }

  addClass = (obj, cls) => {
    if (obj && cls) {
      if (!obj.className.match(cls)) obj.className += " " + cls
    }
  }

  removeClass = (obj, cls) => {
    if (obj && cls) {
      if (obj.className.match(cls)) obj.className = obj.className.replace(" " + cls, "")
    }
  }

  render() {
    const { collapsed } = this.state;
    this.links = []
    return (
      <nav id="Menu" className={"navbar sticky-top"} ref={el => this.Menu = el}>
        <div className="container custom-width">
          <Brand ref={e => this.Logo = e} disabled={false} />
          <svg viewBox="0 0 70 50" id="hamburger-menu" ref={el => this.MenuBtn = el} onClick={this.toggleMenu}>
            <line x1={69} y1={5} x2={1} y2={5}/>
            <line x1={69} y1={25} x2={1} y2={25}/>
            <line x1={69} y1={45} x2={1} y2={45}/>
          </svg>
        </div>
        {/* <div className="container" style={{ pointerEvents: 'none' }}>
          { !collapsed && <BackgroundPattern /> }
        </div> */}
        <div className="container subcontainer" ref={e => this.subcontainer = e}>
          <div className="col-12 offset-md-1 col-md-10" ref={el => this.linkContainer = el} style={{display: "none"}}>
            { this.pages.map((item, index) => (
                <div className="row" key={index}>
                  { this.getLink(item, index) }
                </div>
            ))}
          </div>
        </div>
      </nav>
    );
  }
}

Menu.propTypes = {
  device: PropTypes.string
};

Menu.defaultProps = {
  device: "desktop"
};

export default Menu;